import { DefaultApolloClient } from '@speedlo/graphql'
import debug from 'debug'
import { getPath, getRoot, types } from 'mobx-state-tree'

import { TRootModel } from './RootModel'

export const BaseModel = types.model().views(self => ({
  get log() {
    return debug(`model${getPath(self)}`)
  },
  get root() {
    return getRoot<TRootModel>(self)
  },
  get client() {
    return this.root.apolloClient as DefaultApolloClient
  },
}))
