import {
  createAuthLink,
  createDefaultApolloClient,
  createLogLink,
  createNetworkLink,
  createOfflineLink,
  createRetryLink,
} from '@speedlo/graphql'

import { appConfig } from '../config'
import { TRootModel } from '../models/RootModel'

export function createApolloClient(root: TRootModel) {
  return createDefaultApolloClient(
    createAuthLink({
      enableTokenCache: false,
      getToken: () => root.auth.token,
      onInvalidToken: () => root.auth.invalidate(),
    }),
    createLogLink(),
    createRetryLink(),
    createOfflineLink(),
    createNetworkLink(appConfig.endpoint),
  )
}
