import React from 'react'
import { Redirect } from 'react-router-dom'

import { useRoot } from '../atoms/RootProvider'
import { RedirectToLogin } from './RedirectToLogin'

export const RedirectAfterLogout = () => {
  const { auth, user, order, createOrder, settings, zone } = useRoot()
  if (auth.isAuthenticated) {
    auth.invalidate()
    user.invalidate()
    order.invalidate()
    createOrder.invalidate()
    settings.invalidate()
    zone.invalidate()
    return <RedirectToLogin useOrigin={false} />
  }
  return <Redirect to="/" />
}
