import { ExitToApp } from '@material-ui/icons'
import { SECOND, useTimer } from '@speedlo/hooks'
import { DF } from '@speedlo/tools'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

import { useRoot } from '../atoms/RootProvider'
import { useQLogoutWidget } from '../graph/generated'
import { i18n } from '../i18n'
import { Colors } from '../styling'

const StyledLogoutWidget = styled.div`
  margin-left: 1rem;
  color: ${Colors.greyLight};
  &:hover {
    color: ${Colors.white};
  }

  & span.name {
    color: ${Colors.greyLight};
    text-decoration: none;
    font-weight: lighter;
    position: relative;
    top: 0.6rem;
    display: inline-block;
    max-width: 10rem;
    text-align: right;
  }

  & svg {
    color: ${Colors.greyLight};
    vertical-align: top;
    margin: 0.4rem;
  }

  & div.time {
    font-weight: bold;
    font-size: 1.3rem;
  }
`

export const LogoutWidget = observer(() => {
  const { auth } = useRoot()
  const { data, loading } = useQLogoutWidget(
    { id: auth.userId },
    { skip: !auth.isAuthenticated },
  )

  let displayName
  if (loading) {
    displayName = i18n.t`Not logged in`
  } else {
    displayName = `${data.user.firstName} ${data.user.lastName}`
  }

  const now = useTimer({ enabled: true, interval: SECOND })

  return (
    <StyledLogoutWidget data-testid="logout-widget">
      <Link to="/logout">
        <span className="name">{displayName}</span>
        <ExitToApp />
      </Link>
      <div className="time">{DF.format(now, 'H:mm:ss')}</div>
    </StyledLogoutWidget>
  )
})
